@use 'base/mixins' as m;
@use 'base/variables' as v;

.eventos-registro {
    @include m.contenedor;

    @include m.tablet {
        display: flex;
        align-items: flex-start;
        gap: 2rem;
    }

    &__listado {
        @include m.tablet {
            flex: 0 0 60%;
        }
    }

    &__grid {
        @include m.grid(1, 3rem);
        @include m.tablet {
            @include m.grid(2, 3rem);
        }
    }

    &__heading {

        &--conferencias,
        &--workshops {
            white-space: nowrap;
            @include m.texto-fluido(2rem, 5rem);
        }
        &--conferencias {
            color: v.$azul;
        }

        &--workshops {
            color: v.$secundario;
            margin-top: 5rem;
        }
    }

    &__fecha {
        margin: 6rem 0 3rem 0;
    }
}

.registro {
    background-color: v.$gris-claro;
    padding: 3rem;
    border-radius: 1rem;
    
    @include m.tablet {
        flex: 0 0 40%;
        position: -webkit-sticky;
        position: sticky;
        top: 5rem;
    }

    &__heading {
        margin: 0 0 5rem 0;
    }
}